<template>
  <div class="app-container">
    <div class="filter-container">
      <el-button @click="handleShow('','',0)">新增模块信息</el-button>
    </div>
    <el-table :data="list" v-loading.body="listLoading" element-loading-text="拼命加载中" border fit highlight-current-row>
      <el-table-column align="center" label='ID' width="95">
        <template scope="scope">
          {{scope.$index+1}}
        </template>
      </el-table-column>
      <el-table-column label="moduleId">
        <template scope="scope">
          {{scope.row.moduleId}}
        </template>
      </el-table-column>

      <el-table-column label="模块名称" align="center">
        <template scope="scope">
          <span>{{scope.row.moduleName}}</span>
        </template>
      </el-table-column>
      <el-table-column label="创建时间" align="center">
        <template scope="scope">
          {{scope.row.createTime}}
        </template>
      </el-table-column>
      <el-table-column align="center" label="更新时间">
        <template scope="scope">
          <i class="el-icon-time"></i>
          <span>{{scope.row.updateTime}}</span>
        </template>
      </el-table-column>
      <el-table-column label="操作" align="center">
        <template scope="scope">
          <el-button size="small"   @click="handleShow(scope.$index, scope.row, 2)">查看</el-button>
          <el-button size="small" type="primary"  @click="handleShow(scope.$index, scope.row, 1)">编辑</el-button>
          <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">

    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page="currentPage6"
      :page-size="pages.pageSize"
      :page-sizes="[10, 20, 30, 40]"
      layout="total, sizes, prev, pager, next, jumper"
      :total="pages.total">
    </el-pagination>
  </div>

    <!-- Form -->

    <el-dialog title="新增模块" :visible.sync="dialogFormVisible">
      <el-form :model="form">
        <el-form-item label="模块名称" :label-width="formLabelWidth">
          <el-input v-model="form.moduleName" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="dialogFormVisible = false">取 消</el-button>
        <el-button type="primary" @click="handleSure()">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getModuleList, insertModule, updateModule, deleteModule } from '@/api/basic'

export default {
  data() {
    return {
      list: null,
      listLoading: true,
      isEdit: 0,     //用于判断新增或修改  0 insert  1 update  2 查看
      form: {
        moduleName: ''
      },
      params:{
        //TODO 用于id操作
        moduleId:''
      },
      pages:{
        total:'',
        pageNum:1,
        pageSize:10
      },
      dialogFormVisible: false
    }
  },
  created() {
    this.fetchData()
  },
  methods: {
    fetchData() {
      this.listLoading = true
      getModuleList(this.pages).then(response => {
        console.log(response);
        this.pages.total=response.data.totalPage
        this.list = response.data.result
        this.listLoading = false
      })
    },
    insertModule() {
      return new Promise((resolve, reject) => {
        insertModule(this.form).then(response => {
          console.log(12);
          this.dialogFormVisible = false
          resolve()
          this.fetchData()
        }).catch(error => {
          reject(error)
        })
      })
    },
    updateModule() {
      return new Promise((resolve, reject) => {
        updateModule(this.form).then(response => {
          this.dialogFormVisible = false
          resolve()
          this.fetchData()
        }).catch(error => {
          reject(error.msg)
        })
      })
    },
    deleteModule() {
      return new Promise((resolve, reject) => {
        deleteModule(this.params).then(response => {
          this.dialogFormVisible = false
          resolve()
          this.fetchData()
        }).catch(error => {
          reject(error.msg)
        })
      })
    },
    handleShow(index,row,edit){
        this.dialogFormVisible = true
        if(edit==0){
          this.isEdit=0;
          this.form.moduleName=row.moduleName;
        }else if(edit==1){
           this.form=row;
           this.isEdit=1;
        }else{
          this.form=row;
          this.isEdit=2;
        }
    },
    handleSure(){
      console.log(this.isEdit+'====');
      if(this.isEdit==0){
        //新增
        this.insertModule();
      }else if(this.isEdit==1){
        //修改
        this.updateModule();
      }else{
        //查看

      }
    },
    handleDelete(index,row){
        this.$confirm('确认删除？')
            .then(_ => {
              console.log(row.moduleId+'======');
              this.params.moduleId=row.moduleId;
              this.deleteModule();
              done();
            })
            .catch(_ => {

            });
    },
    handleSizeChange(size){
      this.pages.pageSize=size
      this.fetchData();
    },
    handleCurrentChange(currentPage){
      this.pages.pageNum=currentPage
      this.fetchData();
    }
  }
}
</script>



